import React, { Component } from 'react';

import ClientReview from '../../components/ClientReview';
import CustomBanner from '../../components/CustomBanner';

import axios from '../../clients/axios';

class ReviewsPage extends Component {

    state = {
        reviews: []
    }

    async componentDidMount() {
        const { data: reviews } = await axios.get('/reviews');
        console.log(reviews);
        this.setState({ reviews });
    }

    renderClientReviewRow = _e => {
        const reviews = this.state.reviews.filter(r => r.approved);

        const renderReviews = [];
        for (let i = 0; i < reviews.length; i = i + 2) {
            const rowReviews = [];
            rowReviews.push(reviews.slice(i, i + 2));
            renderReviews.push(rowReviews);
        }

        const renderedReviewsDivs = [];

        renderReviews.forEach(review => {
            if (review[0].length === 2) {
                renderedReviewsDivs.push(
                    <div key={review[0][0].id} className="row" style={{ paddingBottom: 15 }}>
                        <div className="col-sm">
                            <ClientReview
                                text={review[0][0].review}
                                client={review[0][0].client}
                            />
                            <div className="col-sm">
                            </div>
                        </div>
                        <div className="col-sm">
                            <ClientReview
                                text={review[0][1].review}
                                client={review[0][1].client}
                            />
                        </div>
                    </div>);
            } else {
                renderedReviewsDivs.push(
                    <div key={review[0][0].id} className="row" style={{ paddingBottom: 15 }}>
                        <div className="col-sm">
                            <ClientReview
                                text={review[0][0].review}
                                client={review[0][0].client}
                            />
                            <div className="col-sm">
                            </div>
                        </div>
                    </div>);
            }
        });

        return renderedReviewsDivs;
    }

    render() {

        return (
            <React.Fragment>
                <CustomBanner title={'Commentaires'} url={"/reviews"} />
                <div className="container" style={{ paddingTop: 30 }}>
                    {
                        this.renderClientReviewRow()
                    }
                </div>
            </React.Fragment>
        );
    }
};

export default ReviewsPage;